import React from 'react';
import { Link } from 'react-router-dom';

import Field from '../../../Containers/UIElement/Forms/Field/FieldContainer';
import FormContainer from '../../../Containers/UIElement/Forms/FormContainer/FormContainerContainer';
import ButtonForm from './ButtonForm/ButtonForm';

const FormSignIn = ({ loginAttempt }) => {
  return (
    <FormContainer onSubmitEvent={loginAttempt}>
      <Field
        label="Email"
        name="email"
        type="email"
        placeholder="Email"
        requiredlabel={true}
        required
      />
      <Field
        label="Mot de passe"
        name="password"
        type="password"
        requiredlabel={true}
        placeholder="Mot de passe"
        pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}"
        title="Le mot de passe doit être au moins de 8 caractères, contenir une majuscule et un nombre"
        required
      />
      <ButtonForm type="submit">connexion</ButtonForm>
      <Link to="/recovery" className="recovery-link">
        Vous avez oublié votre mot de passe ?
      </Link>
    </FormContainer>
  );
};

export default FormSignIn;
